.main-container {
    display: flex;
    flex-wrap: wrap;
    height: 100vh;
}

/* ----- Loader ----- */
.fullscreen-loader {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: black;
    z-index: 100;
}

.fullscreen-loader-content {
    position: absolute;
    text-align: center;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.fullscreen-loader-image {
    width: 200px;
    height: 200px;
    animation: rotate 1s linear infinite;
}

@keyframes rotate {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

/* ----- Server Bar ----- */
.server-bar {
    background-color: var(--color-server-bar);

    padding: 7px;
    padding-top: 0px;

    width: var(--server-bar-width);
    max-height: 100%;

    display: flex;
    flex-direction: column;
}

.server-bar-icon:hover {
    border-radius: 10px;
}

.server-bar-list {
    flex: 1;

    overflow-y: scroll;
    scrollbar-width: none;
}

/* ----- Channel Bar ----- */
.channel-bar {
    box-sizing: border-box;
    
    width: var(--channel-bar-width);
    height: 100%;

    border-bottom-left-radius: 10px;
    border-top-left-radius: 10px;
    
    background-color: var(--color-channel-bar);
}

.channel-bar-container {
    display: flex;
    flex-direction: column;
    border-top-left-radius: 10px;

    max-height: 100%;
    
    background-color: var(--color-channel-bar);
    width: var(--channel-bar-width);
}

.channel-bar-title {
    box-sizing: border-box;

    white-space: nowrap;
    font-size: 1.5em;
    
    padding: 10px;
    overflow: hidden;
    width: 100%;
    min-height: 48px;
    height: 48px;

    border-bottom-style: solid;
    border-color: var(--color-accent-primary);
    border-top-left-radius: 10px;
}

.channel-bar-list {
    flex: 1;
    box-sizing: border-box;
    overflow-y: auto;
    height: 100%;
}

.channel-list-item {
    transition: 0.2s;
    padding: 10px;
    margin: 5px;
    border-radius: 10px;
}

.channel-list-item:hover:not(.channel-list-item-selected), 
.channel-list-me-item:hover:not(.channel-list-item-selected) {
    background-color: var(--color-channel-bar-item-hover);
}

/* ----- Main Content ----- */
.main-part {
    flex: 1;
    display: flex;
    height: 100vh;
    box-sizing: border-box;
    padding: 5px;
    overflow: hidden;
}

.main-content {
    width: 100%;
    height: 100%;

    overflow: hidden;
    box-sizing: border-box;

    display: flex;
    flex: 1;
    flex-direction: column;
}

.main-content-header {
    box-sizing: border-box;
    height: var(--header-bar-height);
    width: 100%;

    font-size: 1.5em;
    white-space: nowrap;

    padding: 10px;

    z-index: 10;

    border-bottom-style: solid;
    border-top-right-radius: 10px;
    background-color: var(--color-channel-bar);
    border-color: var(--color-accent-primary);
}

.main-content-messages-container {
    width: 100%;
    height: 100%;

    overflow: hidden;
    box-sizing: border-box;

    flex: 1;
    display: flex;
    flex-direction: column;
}

.main-content-messages {
    flex: 1;

    width: 100%;
    height: 100%;
    overflow-y: auto;
    overflow-x: hidden;

}

/* ----- Main Content: Input Area ----- */
.input-area {
    box-sizing: border-box;
    margin-right: 10px;
}

.input-area-content {
    background-color: var(--color-input-area-background);

    border-radius: 10px;
    box-sizing: border-box;

    padding: 10px;
    margin: 10px;
    margin-top: 0px;

    width: 100%;

    display: flex;
    gap: 10px;
}

.input-area-content-input {
    flex: 1;
    box-sizing: border-box;
    padding-left: 5px;
    padding-right: 5px;
    background-color: var(--color-input-textarea-background);
    color: var(--color-input-textarea-color)
}