:root {
    /* 
        Variables: COLORS
    */

    --font-family: Arial, Helvetica, sans-serif;

    /* ----- Text ----- */
    --color-text-primary: #fff;
    --color-text-secondary: rgba(255, 255, 255, 0.7);
    --color-text-disabled: rgba(255, 255, 255, 0.5);

    /* ----- Backgrounds ----- */
    --color-background: black;
    --color-container: rgba(50, 50, 50, 0.7);

    /* ----- Inputs ----- */
    --color-input-primary: rgba(255, 255, 255);
    --color-input-hover: rgba(255, 255, 255, 0.5);
    --color-input-selected: rgba(255, 255, 255, 0.25);

    --color-input-text: black;
    --color-input-text-hover: #222;
    --color-input-text-selected: white;

    /* ----- Accent ----- */
    --color-accent-primary: red;

    /* ----- Tooltip Colors ----- */
    --color-tooltip-background: #333;
    --color-tooltip-content: white;

    /* ----- MainApp Colors ----- */
    --color-server-bar: #111;
    --color-channel-bar: #222;
    --color-channel-bar-item: #333;
    --color-channel-bar-item-hover: #444;

    /* ----- Modal Data ----- */
    --color-modal-backdrop: rgba(0, 0, 0, 0.5);
    --color-modal-background: #222;

    /* ----- Input Area ----- */
    --color-input-area-background: #333;
    --color-input-textarea-background: inherit;
    --color-input-textarea-color: white;

    /* 
        Variables: OTHER
    */

    /* ----- Sizings ----- */
    --server-bar-width: 50px;
    --channel-bar-width: 348px;
    --header-bar-height: 48px;
}

body {
    background-color: var(--color-background);
    color: var(--color-text-primary);
    margin: 0;
    box-sizing: border-box;
    overflow: hidden;
    font-family: var(--font-family);
}

.container {
    background-color: var(--color-container);
    padding: 10px;
}

.button {
    background-color: var(--color-input-primary);
    color: var(--color-input-text);

    border-style: none;
    border-radius: 7px;
    padding: 5px 10px;
    transition: 0.2s;
    border-bottom-style: solid;
    border-bottom-width: 1px;
}

.button:hover {
    background-color: var(--color-input-hover);
    color: var(--color-input-text-hover);
    border-bottom-color: var(--color-accent-primary);
}

.button:active {
    transition: 0.1s;
    background-color: var(--color-input-selected);
    color: var(--color-input-text-selected);
}

iframe {
    display: none;
}

.jumbo {
    display: block;
    width: 100%;
    padding: 10px;
}


.page-center {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}